<template>
    <div class="login">
        <form action="/api/user-service/login" class="login-form" method="post">
            <h2>登录</h2>
            <div class="user-name">
                <label for="">邮箱</label
                ><input
                    class="input"
                    v-model="email"
                    type="text"
                    placeholder="请输入邮箱"
                />
            </div>
            <div class="password">
                <label for="">密码</label
                ><input
                    class="input"
                    v-model="password"
                    type="password"
                    placeholder="请输入用户名"
                />
            </div>
            <h2>
                <input type="button" @click.stop="handleLogin" value="提交" />
                <router-link to="/register">注册</router-link>
            </h2>
        </form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            email: "309128090@qq.com",
            password: "123456"
        };
    },
    created() {
        this.$store.commit("RESETUSER");
    },
    methods: {
        async handleLogin() {
            let data = await this.$store.dispatch("Login", {
                email: this.email,
                password: this.password
            });

            let redirect = this.$route.query.redirect;

            if (redirect) {
                location.href = "/public/h5.html";
            } else {
                this.$router.push("/");
            }

            this.$message({
                message: "登录成功!",
                showClose: true,
                type: "success"
            });
        }
    }
};
</script>

